import { bgCyan, bgYellow, dim, bgWhite, white } from "ansi-colors";
import { DeviceLogMessage } from "../../protocol/Messages";
import { getDevice } from "./shared/deviceMap";
import { getHeartbeat } from "./utils/heartbeatAnimation";

import {
  cursorTo,
  createInterface,
  clearLine,
  clearScreenDown,
} from "readline";
import { KEYS, registerKeyWatcher } from "./keyWatcher";
import { inspect } from "util";

const int = createInterface(process.stdin, process.stdout);

const originalLog = console.log;

let devMenu;

export function hideCursor(out = process.stdout) {
  out.write("\x1B[?25l");
}

export function showCursor(out = process.stdout) {
  out.write("\x1B[?25h");
}

export function init() {
  registerKeyWatcher({
    key: KEYS.CONTROL_L,
    callback() {
      clearScreen();
      log();
    },
  });
  registerKeyWatcher({
    key: "*",
    callback(key) {
      clearPrompt();
    },
  });
  console.log = log;

  const shortcuts = [
    {
      key: "R",
      description: "reload",
    },
    {
      key: "S",
      description: "reset",
    },
    {
      key: "M",
      description: "open menu",
    },
  ];

  const formatted = shortcuts
    .map((shortcut) => {
      return (
        bgWhite.black(` ${shortcut.key} `) + " " + white(shortcut.description)
      );
    })
    .join("    ");
  const logo = bgCyan.black(" {N} Preview ");
  const prompt = `${logo}    ${formatted}`;
  // const padding = " ".repeat(10); // " ".repeat(columns - unstyle(prompt).length - 1);
  // int.setPrompt(prompt + padding);

  devMenu = () => {
    return `${getHeartbeat()}${prompt}`;
  };

  log();
}

function getDeviceLogBadge(deviceName: string, timestamp: number) {
  const dateTime = new Date(timestamp);
  const time = dateTime.toISOString().substring(11, 19);
  return (
    bgCyan.black(" LOG ") + bgYellow.black(` ${deviceName} `) + " " + dim(time)
  );
}

export function deviceLog(message: DeviceLogMessage) {
  const device = getDevice(message.data.device_id);
  const badge = getDeviceLogBadge(
    device?.name || "unknown",
    message.data.timestamp
  );

  // todo: change message to be an array of args, try parse each as json -> if success use util.inspect to print it...

  const parsedArgs = message.data.args.map((arg) => {
    try {
      return inspect(JSON.parse(arg), {
        colors: true,
        depth: 5,
        compact: false,
      });
    } catch (err) {
      return arg;
    }
  });

  log(`${badge}`, ...parsedArgs);
}

