export function clearScreen() {
  const repeatCount = process.stdout.rows - 2;
  const blank = repeatCount > 0 ? "\n".repeat(repeatCount) : "";
  originalLog(blank);
  cursorTo(process.stdout, 0, 0);
  clearScreenDown(process.stdout);
}
