#!/usr/bin/env node

import { WebSocket } from "ws";
import { FormData } from "formdata-polyfill/esm.min";
import { Blob } from "fetch-blob";
import fetch from "node-fetch";
import express from "express";
import path from "path";
import fs from "fs";
import { spawn, exec } from "child_process";
import {
  MessageType,
  getHeartbeatMessage,
  IPreviewMessage,
  IFile,
  getJoinMessage,
  getRestartMessage,
  getResetMessage,
  getOpenMenuMessage,
  getSessionURL,
} from "@nativescript/preview-protocol";

import { config } from "@nativescript/preview-config";

import { registerKeyWatcher, watchKeys } from "./keyWatcher";
import {
  deviceLog,
  hideCursor,
  init as initLogger,
  log,
  showCursor,
} from "./logger";
import * as qr from "qrcode-terminal";

import { onDeviceJoin, onDeviceLeave } from "./shared/deviceMap";
import { tickHeartbeat } from "./utils/heartbeatAnimation";
import { startWebpackProcess } from "./webpackProcesses";

initLogger();

const PORT = 9876;
let sessionID;
let currentWebSocket: WebSocket;

// hideCursor();

const exitHandler = () => {
  showCursor();
  process.exit();
};

process.on("SIGINT", exitHandler);
process.on("exit", exitHandler);
process.on("SIGUSR1", exitHandler);
process.on("SIGUSR2", exitHandler);

const wait = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

registerKeyWatcher({
  key: "r",
  callback() {
    if (!currentWebSocket) {
      return;
    }
    console.log("RESTART");
    currentWebSocket.send(JSON.stringify(getRestartMessage()));
  },
});

registerKeyWatcher({
  key: "m",
  callback() {
    if (!currentWebSocket) {
      return;
    }
    console.log("OPEN MENU");
    currentWebSocket.send(JSON.stringify(getOpenMenuMessage()));
  },
});

registerKeyWatcher({
  key: "s",
  callback() {
    if (!currentWebSocket) {
      return;
    }
    console.log("RESET");
    currentWebSocket.send(JSON.stringify(getResetMessage()));
  },
});

watchKeys();

async function startOrReconnectSession() {
  if (!sessionID) {
    const res = await fetch(`${config.api}/api/session`, {
      method: "POST",
    }).catch((err) => {
      return {
        ok: false,
        err,
      };
    });

    if (!res.ok) {
      console.log("res failed", res);
      // retry in 1s
      setTimeout(() => startOrReconnectSession(), 1000);
      return;
    }

    sessionID = await (res as any as Response).text();

    console.log("Starting new session with id", sessionID);

    console.log("URL IS", getSessionURL(sessionID));
    qr.setErrorLevel("Q");
    qr.generate(
      getSessionURL(sessionID),
      {
        small: true,
      },
      (qr) => {
        console.log(qr);
      }
    );
  }

  let rejoined = false;
  let startTime = Date.now();
  let heartbeat;

  let rejoin = async () => {
    if (heartbeat) {
      clearInterval(heartbeat);
    }

    if (!rejoined) {
      rejoined = true;
      currentWebSocket = null;

      // Don't try to reconnect too rapidly.
      let timeSinceLastJoin = Date.now() - startTime;
      if (timeSinceLastJoin < 10000) {
        // Less than 10 seconds elapsed since last join. Pause a bit.
        await wait(10000 - timeSinceLastJoin);
      }

      // OK, reconnect now!
      startOrReconnectSession();
    }
  };

  console.log("WS endpoint", `${config.ws}/api/session/${sessionID}/websocket`);
  const ws = new WebSocket(`${config.ws}/api/session/${sessionID}/websocket`);

  ws.on("open", function open() {
    currentWebSocket = ws;

    ws.send(JSON.stringify(getJoinMessage("cli")));

    heartbeat = setInterval(() => {
      ws.send(JSON.stringify(getHeartbeatMessage()));
    }, 5000);
  });

  ws.on("message", (message) => {
    const data = JSON.parse(message.toString());

    switch (data.type) {
      case MessageType.DEVICE_JOIN:
        onDeviceJoin(data);
        break;
      case MessageType.DEVICE_LEAVE:
        onDeviceLeave(data);
        break;
      case MessageType.DEVICE_LOG:
        deviceLog(data);
        break;
      case MessageType.HEARTBEAT:
        tickHeartbeat();
        break;

      default:
        console.log(">>>", data);
        break;
    }
  });

  ws.on("close", (event) => {
    console.log("WebSocket closed, reconnecting:", event);
    rejoin();
  });
  ws.on("error", (event) => {
    console.log("WebSocket error, reconnecting:", event);
    rejoin();
  });
}

const serveStatic = express.static(
  path.resolve(__dirname, "./dist/static")
);

const app = express();

app.use(serveStatic);

app.get("/api/session", (req, res) => {
  if (!sessionID) {
    return res
      .status(400)
      .setHeader("Content-Type", "text/plain")
      .send("No sessionID yet.");
  }

  res.status(200).send(sessionID);
});

app.get("/api/evict", (req, res) => {
  console.log("\n\n   ~~ TRIGGER EVICTION ~~\n\n");
  const evictTrigger = path.resolve(
    __dirname,
    "../../preview-worker/src/.evict_trigger"
  );
  fs.appendFileSync(evictTrigger, Date.now().toString() + "\n");

  res.status(200).send("ok");
});

app.get("/api/launch", (req, res) => {
  if (!sessionID) {
    return res
      .status(400)
      .setHeader("Content-Type", "text/plain")
      .send("No sessionID yet.");
  }

  console.log("LAUNCHING IN SIM");
  const url = getSessionURL(sessionID);
  console.log(`\n${url}\n`);
  exec(`xcrun simctl openurl booted "${url}"`);

  res.status(200).send("ok");
});

app.listen(PORT, async () => {
  await startOrReconnectSession();
  console.log(`"CLI SERVER LISTENING ON http://localhost:${PORT}"`);
  await wait(5000);

  let cnt = 0;
  startWebpackProcess("ios", (message) => {
    // console.log(data);
    if (message?.type !== "files") {
      return;
    }

    if (
      !(message.data.emittedAssets.length || message.data.staleAssets.length)
    ) {
      return;
    }

    const form = new FormData();

    message.data.emittedAssets.map((asset) => {
      const filename = asset.path.replace(/\//g, "~~");
      const blob = new Blob([Buffer.from(asset.contents)], {
        type: asset.type,
      });
      form.append("emittedAssets", blob, filename);
      // form.append("emittedAssets", Buffer.from(asset.contents), {
      //   filename: asset.path.replace(/\//g, "~~"),
      //   contentType: asset.type,
      //   knownLength: asset.size,
      // });
    });

    message.data.staleAssets.map((asset) => {
      form.append("staleAssets", asset);
    });

    console.log("here", form);

    // upload the assets to the session
    const lbl = `UPLOAD ~~ ${++cnt}`;
    console.log(lbl);
    console.time(lbl);
    fetch(`${config.api}/api/session/${sessionID}/upload`, {
      method: "PUT",
      body: form,
    })
      .then((res) => {
        console.log(res.body);
        if (res.ok) {
          console.log("ok.");
        }
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        console.timeEnd(lbl);
      });

    // currentWebSocket?.send(JSON.stringify(data))
    // console.log("WE GOT DEM FILES", data);
  });
});
