import {
  IFile,
  IPreviewMessage,
  MessageType,
} from "@nativescript/preview-protocol";

import { ChildProcess, spawn } from "child_process";

const webpackProcesses = new Map<string, ChildProcess>();

const pathToWebpack = require.resolve(
  `@nativescript/webpack/dist/bin/index.js`
);

export function startWebpackProcess(
  platform: string,
  onMessage: (
    message: IPreviewMessage<
      MessageType.FILES,
      {
        emittedAssets: IFile[];
        staleAssets: string[];
      }
    >
  ) => void
) {
  if (webpackProcesses.has(platform)) {
    return webpackProcesses.get(platform);
  }

  const childProcess = spawn(
    pathToWebpack,
    [
      "build",
      "--watch",
      // "--env.verbose",
      `--env.${platform}`,
      "--env.preview",

      "--env.appPath=app",
      "--env.hmr=true",
    ],
    {
      cwd: process.cwd(),
      // stdio: [null, null, null, "ipc"],
      stdio: ["inherit", "inherit", "inherit", "ipc"],
    }
  );

  // restart process automatically...
  childProcess.on("exit", () => {
    webpackProcesses.delete(platform);

    // restart...
    startWebpackProcess(platform, onMessage);
  });

  childProcess.on("message", onMessage);

  webpackProcesses.set(platform, childProcess);
  return childProcess;
}
