import {
  DeviceJoinMessage,
  DeviceLeaveMessage,
  IDevice,
} from "@nativescript/preview-protocol";

export const deviceMap = new Map<string, IDevice>();

export function onDeviceJoin(message: DeviceJoinMessage) {
  deviceMap.set(message.data.id, message.data);
}

export function onDeviceLeave(message: DeviceLeaveMessage) {
  deviceMap.delete(message.data.id);
}

export function getDevice(id: string): IDevice {
  if (deviceMap.has(id)) {
    return deviceMap.get(id);
  }

  return null;
}
