export enum MessageType {
  HEARTBEAT = "heartbeat",
  JOIN = "join",
  LEAVE = "leave",
  DEVICE_JOIN = "device_join",
  DEVICE_LEAVE = "device_leave",
  INITIAL_FILES = "initial_files",
  FILES = "files",
  DEVICE_LOG = "device_log",
  RESTART = "restart",
  RESET = "reset",
  OPEN_MENU = "open_menu",
}

export interface IPreviewMessage<T = string | string, D = any> {
  type: T;
  data: D;
}

export interface ITimestamp {
  timestamp: number;
}

export interface IDevice {
  id: string;
  name: string;
  platform: "ios" | "android" | string;
  version: string;
}

export interface IFile {
  path: string;
  type: string;
  size: number;
  contents: any;
}

export interface IAssets {
  emittedAssets: string[];
  staleAssets: string[];
}

export interface HeartbeatMessage
  extends IPreviewMessage<MessageType.HEARTBEAT, ITimestamp> {}
export interface JoinMessage
  extends IPreviewMessage<MessageType.JOIN, { name: string }> {}
export interface LeaveMessage
  extends IPreviewMessage<MessageType.LEAVE, { name: string }> {}
export interface DeviceJoinMessage
  extends IPreviewMessage<MessageType.DEVICE_JOIN, IDevice> {}
export interface DeviceLeaveMessage
  extends IPreviewMessage<MessageType.DEVICE_LEAVE, { id: string }> {}
export interface InitialFilesMessage
  extends IPreviewMessage<MessageType.INITIAL_FILES, { files: string[] }> {}
export interface FilesMessage
  extends IPreviewMessage<MessageType.FILES, IAssets> {}
export interface DeviceLogMessage
  extends IPreviewMessage<
    MessageType.DEVICE_LOG,
    { device_id: string; type: string; args: string[] } & ITimestamp
  > {}
export interface RestartMessage
  extends IPreviewMessage<MessageType.RESTART, {}> {}

export interface ResetMessage extends IPreviewMessage<MessageType.RESET, {}> {}
export interface OpenMenuMessage
  extends IPreviewMessage<MessageType.OPEN_MENU, {}> {}

function getTimestamp(): number {
  return new Date().getTime();
}

export function getHeartbeatMessage(
  timestamp: number = getTimestamp()
): HeartbeatMessage {
  return {
    type: MessageType.HEARTBEAT,
    data: {
      timestamp,
    },
  };
}

export function getJoinMessage(name: string): JoinMessage {
  return {
    type: MessageType.JOIN,
    data: {
      name,
    },
  };
}

export function getLeaveMessage(name: string): LeaveMessage {
  return {
    type: MessageType.LEAVE,
    data: {
      name,
    },
  };
}

export function getDeviceJoinMessage(device: IDevice): DeviceJoinMessage {
  return {
    type: MessageType.DEVICE_JOIN,
    data: {
      ...device,
    },
  };
}

export function getDeviceLeaveMessage(id: string): DeviceLeaveMessage {
  return {
    type: MessageType.DEVICE_LEAVE,
    data: {
      id,
    },
  };
}

export function getInitialFilesMessage(files: string[]): InitialFilesMessage {
  return {
    type: MessageType.INITIAL_FILES,
    data: {
      files,
    },
  };
}

export function getFilesMessage(data: IAssets): FilesMessage {
  return {
    type: MessageType.FILES,
    data,
  };
}

export function getDeviceLogMessage(
  device_id: string,
  type: string,
  args: string[]
): DeviceLogMessage {
  return {
    type: MessageType.DEVICE_LOG,
    data: {
      device_id,
      timestamp: getTimestamp(),
      type,
      args,
    },
  };
}

export function getRestartMessage(): RestartMessage {
  return {
    type: MessageType.RESTART,
    data: {},
  };
}

export function getResetMessage(): ResetMessage {
  return {
    type: MessageType.RESET,
    data: {},
  };
}

export function getOpenMenuMessage(): OpenMenuMessage {
  return {
    type: MessageType.OPEN_MENU,
    data: {},
  };
}
