import { bgYellow } from "ansi-colors";
import { drawMenu } from "../logger";

let tick = false;
const chars = [bgYellow.black(" \u2039 "), bgYellow.black(" \u203a ")];

export function tickHeartbeat() {
  tick = !tick;

  drawMenu();
}

export function getHeartbeat() {
  return chars[tick ? 0 : 1];
}
