const isWindows = process.platform === "win32";

export const KEYS = {
  NULL: "\u0000",
  ARROW_DOWN: "\u001b[B",
  ARROW_LEFT: "\u001b[D",
  ARROW_RIGHT: "\u001b[C",
  ARROW_UP: "\u001b[A",
  BACKSPACE: Buffer.from(isWindows ? "08" : "7f", "hex").toString(),
  CONTROL_C: "\u0003",
  CONTROL_D: "\u0004",
  CONTROL_U: "\u0015",
  CONTROL_L: "\u000C",
  ENTER: "\r",
  ESCAPE: "\u001b",
};

export interface KeyWatcherEntry {
  key: string;
  callback: (key: string) => void;
}

let keyWatcherMap = new Map<string, KeyWatcherEntry>();

export function registerKeyWatcher(watcherEntry: KeyWatcherEntry) {
  if (keyWatcherMap.has(watcherEntry.key)) {
    console.warn(`Duplicate key watcher for "${watcherEntry.key}", ignoring.`);
    return;
  }

  keyWatcherMap.set(watcherEntry.key, watcherEntry);
}

export function watchKeys(stdin = process.stdin, stdout = process.stdout) {
  const onKeypress = (key: string) => {
    if (key === KEYS.CONTROL_C || key === KEYS.CONTROL_D) {
      if (typeof stdin.setRawMode === "function") {
        stdin.setRawMode(false);
      }

      stdout.write("\n");
      process.exit(0);
      return;
    }

    if (keyWatcherMap.has(key)) {
      const entry = keyWatcherMap.get(key);

      entry.callback(key);
    }

    // todo: allow multiple watchers for *
    if (keyWatcherMap.has("*")) {
      const entry = keyWatcherMap.get("*");

      entry.callback(key);
    }
  };

  if (typeof stdin.setRawMode === "function") {
    stdin.setRawMode(true);
    stdin.resume();
    stdin.setEncoding("utf8");
    stdin.on("data", onKeypress);
  }
}
